	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Flens","date":"14 Dec 1999","slug":"/1999-12-14-flens/","tags":"Y1999"},"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/1999/12/flens.jpg\" height=\"392\" width=\"300\" border=\"0\" alt=\"flens.jpg: Flens\" /></p>\n<p>If <a href=\"http://marcsvoice.editthispage.com/1999/12/12\">Marc's Voice</a> brings the food, I'll bring the beer!</p>\n<p>Everything's going to be alright. My forms for VA Linux were lost, but I might get in anyway. My folks are now receiving E-mail (I'm not sure whether Curly helped them or not). Our stock is up from 7 to 14.60 Euros, and noone seems to know why.</p>"},"next":{"fields":{"title":"Hi Mom!","date":"13 Dec 1999","slug":"/1999-12-13-hi-mom/"}},"previous":{"fields":{"title":"Feet","date":"15 Dec 1999","slug":"/1999-12-15-feet/"}}},{"node":{"fields":{"title":"Hi Mom!","date":"13 Dec 1999","slug":"/1999-12-13-hi-mom/","tags":"Y1999"},"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/1999/12/fpwindow.jpg\" height=\"285\" width=\"300\" border=\"0\" alt=\"fpwindow.jpg: \" /></p>\n<p>Mama is looking at me instead of out the window at the neighbor's horse shed in back. </p>\n<p>At my urging, my parents in Minnesota got a computer a couple of weeks ago and are now nearly in the Internet... they can browse web pages, but can't yet send and receive mail. That reminds me how hard the Internet still is, despite the attempts to make it easy. I'm sure their provider gives their new customers all the technical data that is needed to sign on, but that certainly doesn't mean that a beginner can figure what do to if (or when) something goes wrong. All we want to do is to be able to send them baby pictures.</p>\n<p><i>Mom and Dad: That's Curly from the Three Stooges in the left margin. He knows how it feels to be connecting to the Internet for the first time. Just click on his face! (and make sure your sound is turned on.)</i></p>"},"next":{"fields":{"title":"Alsterhaus Christmas lights","date":"12 Dec 1999","slug":"/1999-12-12-alsterhaus-christmas-lights/"}},"previous":{"fields":{"title":"Flens","date":"14 Dec 1999","slug":"/1999-12-14-flens/"}}},{"node":{"fields":{"title":"Alsterhaus Christmas lights","date":"12 Dec 1999","slug":"/1999-12-12-alsterhaus-christmas-lights/","tags":"Y1999"},"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/1999/12/alsterhaus400.jpg\" height=\"319\" width=\"400\" border=\"0\" alt=\"alsterhaus400.jpg: Alsterhaus department store in Hamburg\" /></p>\n<p>The Alsterhaus department store is on the Jungfernstieg, direct on the Inner Alster lake, downtown Hamburg.</p>"},"next":{"fields":{"title":"Christmas cards....","date":"11 Dec 1999","slug":"/1999-12-11-christmas-cards/"}},"previous":{"fields":{"title":"Hi Mom!","date":"13 Dec 1999","slug":"/1999-12-13-hi-mom/"}}}],"pathPrefix":"","first":false,"last":false,"index":1099,"pageCount":1101}}

